<!-- eslint-disable vue/no-mutating-props -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<!-- eslint-disable max-len -->
<!-- eslint-disable linebreak-style -->
<template>
  <form class="d-flex" role="search" action="#" @submit.prevent="submit">
    <input class="form-control" type="search" placeholder="Поиск по названию картины"
      aria-label="Поиск по названию картины" v-model="currentSearchString">
    <button class="btn btn-dark" type="submit">Найти</button>
  </form>
</template>

<script>

export default {
  data() {
    return {
      currentSearchString: '',
    };
  },
  props: ['searchString'],
  computed: {
  },
  watch: {
    searchString(value) {
      this.currentSearchString = value;
    },
  },
  methods: {
    submit() {
      this.$emit('update:searchString', this.currentSearchString);
    },
  },
};
</script>
