<!-- eslint-disable vuejs-accessibility/click-events-have-key-events -->
<!-- eslint-disable max-len -->
<!-- eslint-disable linebreak-style -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <div class="card">
    <img :src="item.image[0]" class="card-img-top" @click="showModal = true" alt="...">
    <div class="card-body">
      <h5 class="card-title" @click="showModal = true">{{ item.title }}</h5>
      <div class="purchase__wrapper" v-if="!item.sold">
        <div class="price__wrapper">
          <span class="past__price" v-if="item.lastPrice">{{ item.lastPrice }} $</span>
          <span class="now__price">{{ item.nowPrice }} $</span>
        </div>
        <a href="#" class="btn btn-dark btn-buy" @click="buy(item)" v-if="!item.inCart">
          <div class="lds-dual-ring" v-if="loading"></div>
          Купить
        </a>
        <a href="#" class="btn btn-dark btn-in-cart" v-if="item.inCart">
          <svg width="16" height="13" viewBox="0 0 16 13" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M14.5315 1.80937L5.63341 11.237L1.34814 7.19237" stroke="#F4F6F9" stroke-width="2"
              stroke-linecap="round" stroke-linejoin="round" />
          </svg>
          В корзине
        </a>
      </div>
      <span class="sold center" v-else>Продана на аукционе</span>
    </div>
    <div v-if="showModal" @close="showModal = false">
      <div class="modal-mask">
        <div class="modal-wrapper">
          <div class="modal-container">

            <div class="modal-header">
              <slot name="header">
                {{ item.title }}
              </slot>
            </div>
            <div class="modal-body">
              <slot name="body">
                <p class="modal-text">
                  Lorem ipsum dolor sit, amet consectetur adipisicing elit. Voluptate deserunt, pariatur totam laudantium
                  eaque possimus maxime explicabo placeat! Adipisci, aspernatur.
                </p>
              </slot>
              <slider animation="fade" :control-btn="false" style="height: 230px;">
                <slider-item  v-for="img in item.image"  :key="img" :style="i" @click="hello">
                  <img :src="img" class="card-img" @click="showModal = true" alt="...">
                </slider-item>
              </slider>
            </div>

            <div class="modal-footer">
              <slot name="footer">
                <button class="btn btn-dark modal-default-button" @click="showModal = false">
                  OK
                </button>
              </slot>
            </div>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import { Slider, SliderItem } from 'vue-easy-slider';

export default {
  data() {
    return {
      loading: false,
      currentCart: [],
      showModal: false,
      list: [
        { backgroundColor: '#3f51b5', width: '100%', height: '100%' },
        { backgroundColor: '#eee', width: '100%', height: '100%' },
        { backgroundColor: '#f44336', width: '100%', height: '100%' },
      ],
    };
  },
  components: {
    Slider,
    SliderItem,
  },
  props: ['item', 'cart'],
  watch: {
    cart(value) {
      this.currentCart = value;
    },
  },
  methods: {
    hello($event) {
      console.log(`hello index: ${$event}`);
    },
    buy(item) {
      this.loading = true;
      setTimeout(() => {
        // eslint-disable-next-line no-param-reassign
        item.inCart = true;
        this.loading = false;
        this.currentCart = this.cart || [];
        this.currentCart.push({ item });
        localStorage.setItem('cart', JSON.stringify(this.currentCart));
      }, 2000);
    },
  },
};
</script>
