<script>
// eslint-disable-next-line import/extensions, import/no-unresolved
import paintings from '@/data/paintings';
// eslint-disable-next-line import/no-unresolved
import itemFilter from '@/components/itemFilter.vue';
// eslint-disable-next-line import/no-unresolved
import paintItem from '@/components/paintItem.vue';

export default {
  name: 'App',
  components: { itemFilter, paintItem },
  data() {
    return {
      searchString: '',
      loading: false,
      cart: [

      ],
    };
  },
  computed: {
    paintings() {
      return this.filter.map((item) => ({ ...item, loading: false }));
    },
    filter() {
      const result = paintings.filter((item) => item.title.includes(this.searchString));
      return result;
    },
  },
  methods: {
    buy(item) {
      console.log(item);
      // eslint-disable-next-line no-param-reassign
      item.loading = true;
      setTimeout(() => {
        // eslint-disable-next-line no-param-reassign
        item.inCart = true;
        // eslint-disable-next-line no-param-reassign
        // item.loading = false;
        this.cart = this.cart || [];
        this.cart.push({ item });
        localStorage.setItem('cart', JSON.stringify(this.cart));
      }, 200000);
    },
    ses() {
      if (!localStorage.getItem('id')) {
        const min = Math.ceil(10000);
        const max = Math.floor(100000);
        const id = Math.floor(Math.random() * (max - min)) + min;
        localStorage.setItem('id', id);
      } else {
        this.cart = JSON.parse(localStorage.getItem('cart'));
        if (this.cart) {
          // eslint-disable-next-line no-restricted-syntax, guard-for-in
          for (const item of this.cart) {
            // eslint-disable-next-line no-restricted-syntax, guard-for-in
            for (const i in this.paintings) {
              if (item.item.id === this.paintings[i].id) {
                this.paintings[i].inCart = true;
              }
            }
          }
        }
      }
    },
  },
  created() {
    this.ses();
  },
};
</script>

